<template>
  <div id="settings">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/settings' }"></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
      </template>
      <div>
        <el-row :gutter="20">
          <el-col :span="16">
            <el-form ref="form" :model="user" label-width="80px">
              <el-form-item label="">
                {{user.id}}
              </el-form-item>
              <el-form-item label="">
                {{user.mobile}}
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="user.name"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input type="textarea" v-model="user.desc"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="user.email"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit"></el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="8">
              <el-avatar shape="square"
                         :size="100"
                         fit="cover"
                         :src="user.photo"
                         @click.native="$refs.file.click()"
              ></el-avatar>
            <div class="hint"></div>
            <input type="file" ref="file" hidden @change="onChange">
          </el-col>
        </el-row>
      </div>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :append-to-body="appendToBody"
      @opened="dialogOpended"
      @closed="dialogClosed">
      <div class="preview-img-wrap">
        <img
          class="preview-img"
          :src="previewImg"
          alt=""
          ref="previewImg"
        >
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="sureClick"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
// cropper
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { getUserProfile } from 'network/login'
export default {
  name: 'settings',
  data () {
    return {
      form: {
        // name: '',
        // desc: '',
        // email: ''
      },
      user: {},
      dialogVisible: false,
      appendToBody: true,
      // 
      previewImg: '',
      cropper: '',
      canvasData: null // 
    }
  },
  created () {
    this._getUserProfile()
  },
  methods: {
    // 
    _getUserProfile () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data
      })
    },
    // 
    // 
    onSubmit () {
      console.log('submit!')
      this.form = this.user
      console.log(this.form)
      this.$bus.$emit('onSubmit', this.form)
    },
    // ,dialog
    onChange () {
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImg = blobData
      // change
      this.$refs.file.value = ''
      this.dialogVisible = true
    },
    // Dialog 
    dialogOpended () {
      const image = this.$refs.previewImg
      // 
      this.cropper = new Cropper(image, {
        aspectRatio: 1
      })
    },
    // Dialog 
    dialogClosed () {
      // 
      this.cropper.destroy()
    },
    // 
    sureClick () {
      this.cropper.getCroppedCanvas().toBlob(file => {
        // const formData = new FormData()
        // formData.append('photo', file)
        console.log(file)
        // 
        this.user.photo = window.URL.createObjectURL(file)
        // 
        this.dialogVisible = false
      })
    }
  }
}
</script>

<style scoped>
  .hint {
    height: 20px;
    width: 100px;
    background-color: rgba(0,0,0,.5);
    position: relative;
    top: -25px;
    color: #ffffff;
    font-size: 13px;
    text-align: center;
  }
  .preview-img {
    display: block;
    max-width: 100%;
    height: 200px;
  }
</style>
